.nav {
    width: 100%;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    background-color: tomato;
    color: white;
    padding: 20px 0;
    text-align: center;
    font-family: 'Shrikhand', cursive;
    overflow: hidden;
    -webkit-box-shadow: 0 3px 10px grey;
            box-shadow: 0 3px 10px grey;
}

#navTitle {
    font-size: 200%;
    font-style: bold;
}

#guess {
    margin: 0 15%;
    font-size: 200%;
}

.score {
    font-size: 120%;
}

#scoreLine {
    margin-left: 10px;
    border-left: 2px solid white;
    padding-left: 10px;
}


