.card {
    float: left;
    height: 115px;
    padding: 12px 10px;
    -webkit-box-shadow: 2px 3px 5px grey;
            box-shadow: 2px 3px 5px grey;
    margin: 15px;
}

.card:hover {
    -webkit-box-shadow: 6px 9px 5px grey;
            box-shadow: 6px 9px 5px grey;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.image {
    width: 200px;
    margin: auto;
}

#red {
    -webkit-animation: flashRed 1.5s forwards;
            animation: flashRed 1.5s forwards;
}

#yellow {
    -webkit-animation: flashYellow 1.5s forwards;
            animation: flashYellow 1.5s forwards;
}

@-webkit-keyframes flashRed {
    from {
        background: tomato;
    }
    to {
        background: white;
    }
}

@keyframes flashRed {
    from {
        background: tomato;
    }
    to {
        background: white;
    }
}

@-webkit-keyframes flashYellow {
    from {
        background: goldenrod;
    }
    to {
        background: white;
    }
}

@keyframes flashYellow {
    from {
        background: goldenrod;
    }
    to {
        background: white;
    }
}


